/* Common */
/* header */
header {
  background: #cb1a4f;
  height: 64px; }
  header .navbar, header .hw {
    height: 100%; }
  header .navbar-brand {
    width: 190px;
    margin-right: 54px;
    padding-right: 30px; }
    header .navbar-brand img {
      max-width: 100%; }
  header .header-search-wrapper {
    width: 450px;
    max-width: 450px; }
    header .header-search-wrapper .header-search {
      width: calc(100% - 32px);
      max-width: 410px;
      height: 32px;
      padding-left: 40px;
      background: rgba(0, 0, 0, 0.12);
      border-color: rgba(0, 0, 0, 0);
      font-family: 'Proxima Nova Lt';
      font-size: 14px;
      line-height: normal;
      font-style: normal;
      color: rgba(255, 255, 255, 0.9);
      border-radius: 4px;
      transition: all 0.4s; }
      @media screen and (prefers-reduced-motion: reduce) {
        header .header-search-wrapper .header-search {
          transition: none; } }
      header .header-search-wrapper .header-search.placeholder {
        color: rgba(255, 255, 255, 0.7);
        opacity: 1; }
      header .header-search-wrapper .header-search:-moz-placeholder {
        color: rgba(255, 255, 255, 0.7);
        opacity: 1; }
      header .header-search-wrapper .header-search::-moz-placeholder {
        color: rgba(255, 255, 255, 0.7);
        opacity: 1; }
      header .header-search-wrapper .header-search:-ms-input-placeholder {
        color: rgba(255, 255, 255, 0.7);
        opacity: 1; }
      header .header-search-wrapper .header-search::-webkit-input-placeholder {
        color: rgba(255, 255, 255, 0.7);
        opacity: 1; }
    header .header-search-wrapper .control {
      width: 32px;
      height: 32px;
      background: transparent;
      outline: none;
      box-shadow: none;
      border: none;
      padding: 8px;
      line-height: 0px; }
      header .header-search-wrapper .control:focus, header .header-search-wrapper .control:hover {
        background: transparent;
        outline: none;
        box-shadow: none;
        border: none; }

